@import "./variable";
@import "./mixin.scss";
.app__main {
  // border: 1px solid green;
  display: flex;
  padding: 45px 0;
  flex-wrap: wrap;
  @include Responsive(SM) {
    padding: 0 19px;
    padding-top: 45px;
  }
  &__left {
    width: 25%;
    @include Responsive(SM) {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      padding: 0 35px;
    }
    // @include Responsive(XS) {
    //   display: none;
    // }
    &-link {
      text-decoration: none;
      background-color: #42659d;
      color: white;
      margin: 5px 0 0 10px;
      padding: 8px 10px;
      border-radius: 3px;
      display: inline-block;
      cursor: pointer;
      @include Responsive(SM) {
        height: 20px;
      }
    }
    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
      @include Responsive(SM) {
        text-align: start;
        padding-top: 0;
        width: 50%;
      }
      &:nth-of-type(2),
      &:nth-of-type(3) {
        padding-top: 15px;
        @include Responsive(SM) {
          padding-top: 0;
          width: 50%;
        }
      }
      &__title {
        margin: 0;
        padding-bottom: 10px;
        padding-left: 10px;
        font-size: 17px;
        @include Responsive(SM) {
          padding-left: 0;
        }
      }
      &__item {
        color: #666;
        padding-bottom: 10px;
        input {
        }
        label {
          font-size: 14px;
        }
      }
    }
  }
  &__right {
    width: 75%;
    @include Responsive(SM) {
      width: 100%;
    }
    .product__list {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      &__item {
        padding-bottom: 40px;
        width: 33%;
        text-align: center;
        @include Responsive(SM) {
          width: 49%;
        }
        @include Responsive(XS) {
          width: 99%;
        }
        &-img {
          width: 80%;
          margin: 0 auto;
          padding-top: 100%;
        }
        &-info {
          margin-top: 30px;
          &-name {
            margin: 0;
            font-size: 14px;
            color: #666;
          }
          &-price {
            padding: 10px 0;
            display: flex;
            justify-content: center;
            &-new {
              margin: 0;
              font-weight: 700;
              padding-right: 14px;
            }
            &-old {
              margin: 0;
              text-decoration: line-through;
              color: #999;
            }
          }
          &-button {
            text-decoration: none;
            background-color: #42659d;
            color: white;
            text-transform: uppercase;
            font-size: 14px;
            padding: 7px 15px;
            &:hover {
              background-color: rgba($color: #42659d, $alpha: 0.6);
            }
          }
        }
      }
    }
  }
}
